___CSS_LOADER_EXPORT___.push([module.id, `.jp-ToolbarButton.jp-RevertCheckpoint-button {
  background-color: var(--jp-layout-color1);
}

.jp-RevertCheckpoint-dialog {
  min-width: 300px;
}

.jp-RevertCheckpoint-dialog select {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: var(--jp-ui-font-family);
  font-size: var(--jp-ui-font-size1);
  padding: 8px;
}

.jp-RevertCheckpoint-dialog option {
  padding: 6px;
}

/* 确认对话框样式 */
.jp-RevertCheckpoint-dialog p {
  margin-top: 8px;
  margin-bottom: 8px;
  line-height: 1.5;
}

.jp-RevertCheckpoint-dialog strong {
  font-weight: 600;
}

/* 加载状态指示器 */
.jp-Dialog-busy {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 10000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.jp-Dialog-busy::after {
  content: '';
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 4px solid var(--jp-border-color1);
  border-top-color: var(--jp-brand-color1);
  animation: jp-Dialog-busy-animation 1s infinite linear;
}

@keyframes jp-Dialog-busy-animation {
  to {
    transform: rotate(360deg);
  }
}

/* 新增样式：带有文本标签的按钮 */
.jp-RevertCheckpoint-button.jp-mod-styled {
  background-color: var(--jp-brand-color1);
  color: white;
  border-radius: 3px;
  padding: 0 8px;
  margin: 0 4px;
  display: flex;
  align-items: center;
  height: 24px;
  transition: background-color 0.2s ease;
}

.jp-RevertCheckpoint-button.jp-mod-styled:hover {
  background-color: var(--jp-brand-color0);
}

.jp-RevertCheckpoint-button.jp-mod-styled .jp-ToolbarButtonComponent-label {
  padding-left: 4px;
  font-size: var(--jp-ui-font-size1);
  font-weight: 500;
}
